* {
	font-family: "Roboto", sans-serif; }

$icon_text_color: #1EACC7;
$req_doc_text: #FF5656;

@mixin btn_hover {
	&:hover {
		background-image: none;
		background-color: $icon_text_color; } }


header {
	position: relative;
	height: 50px; }

nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 3; }

.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;

	&_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300; }

	&_item {
		border-right: 2px dotted black;
		padding-right: 28px; } }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0, 0, 0, 0.6);

	&_logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }

	&_official {
		margin-top: 6px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }

	&_call {
		color: #ffffff;
		font-size: 12px;
		font-weight: 300;
		margin-top: 19px;
		text-align: right; }

	&_phone {
		display: block;
		margin-top: 8px;
		color: #ffffff;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px; }

	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }

.promo {
	min-height: 800px;
	background: url("../img/uber_bg.png") center (center / cover) no-repeat;
	padding: 150px 0 135px 0;

	&_header {
		color: #ffffff;
		font-weight: 900;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px;
		margin: 0;
		text-align: center; }

	&_subheader {
		font-weight: 700;
		font-size: 30px;
		text-align: center;
		color: #ffffff;
		margin-top: 12px; }

	&_descr {
		color: #ffffff;
		font-size: 18px;
		font-weight: 200;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }

	&_btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }

.reasons {
	padding: 71px 0 65px 0; }

.label {
	position: relative;
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center;
	// z-index: 2
	&_white {
		background-color: #fff;
		color: #000; } }

.title {
	// position: relative
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center;
	// z-index: 2
	&_white {
		color: #FFFFFF; } }

.subtitle {
	position: relative;
	margin-top: 16px;
	font-weight: 400;
	font-size: 17px;
	line-height: 21px;
	text-align: center;
	color: #222222;
	text-align: center;
	z-index: 2;
	&_white {
		color: #FFFFFF; } }


.reasons .reasons_block {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 35px;
	.reasons_round {
			display: flex;
			justify-content: center;
			align-items: center;
			margin-right: 26px;
			width: 113px;
			height: 113px;
			background-color: $icon_text_color;
			border-radius: 100%; }
	.reasons_descr {
		width: 343px;
		.reasons_subtitle {
			line-height: 21px;
			color: $icon_text_color;
			font-size: 17px;
			font-weight: 400; }
		.reasons_text {
			color: #202020;
			font-size: 15px;
			font-weight: 300;
			line-height: 21px; } } }

.mobile {
	min-height: 864px;
	padding: 48px 0 79px 0;
	background: url('../img/app_bg.png') center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/app_img1.png') center (center / cover) no-repeat; }
		&_2 {
			background: url('../img/app_img2.png') center (center / cover) no-repeat; }
		&_3 {
			background: url('../img/app_img3.png') center (center / cover) no-repeat; }
		&_4 {
			background: url('../img/app_img4.png') center (center / cover) no-repeat; }
		&_5 {
			background: url('../img/app_img5.png') center (center / cover) no-repeat; }
		&_6 {
			background: url('../img/app_img6.png') center (center / cover) no-repeat; }
		&_subtitle {
			font-weight: 400;
			font-size: 24px;
			text-align: center;
			text-transform: uppercase;
			color: #FFFFFF; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 29px;
			height: 29px;
			background-color: #000000;
			margin: 0 auto;
			border-radius: 100%;
			cursor: pointer; } } }

.choise {
	position: relative;
	min-height: 900px;
	padding: 59px 0 137px 0;
	background: url('../img/car_bg.png') center (center / cover) no-repeat;

	img {
		position: relative;
		display: block;
		margin: 56px auto 0 auto;
		z-index: 2; }
	&_descr {
		font-weight: 300;
		font-size: 17px;
		line-height: 24px;
		text-align: center;
		color: #222222; }
	.black {
		position: absolute;
		top: 0;
		left: 0;
		height: 550px;
		width: 100%;
		background-color: #070716;
		z-index: 1; } }

